import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MasterLayoutComponent } from './master-layout/master-layout.component';


const routes: Routes = [
  {
    path: '',
    component: MasterLayoutComponent,
    children: [
      {
        path: 'bin',
        loadChildren: () => import('./manage-bin/manage-bin.module').then(m => m.ManageBinModule)
      },
      {
        path: 'brand',
        loadChildren: () => import('./brand/brand.module').then(m => m.BrandModule)
      },
      {
        path: 'category',
        loadChildren: () => import('./category/category.module').then(m => m.CategoryModule)
      },
      {
        path: 'product',
        loadChildren: () => import('./product/product.module').then(m => m.ProductModule)
      },
      {
        path: 'suppliers',
        loadChildren: () => import('./suppliers/suppliers.module').then(m => m.SuppliersModule)
      },
      {
        path: 'client',
        loadChildren: () => import('./client/client.module').then(m => m.ClientModule)
      },
      {
        path: 'warehouse',
        loadChildren: () => import('./warehouse/warehouse.module').then(m => m.WarehouseModule)
      },
      {
        path: 'aisle',
        loadChildren: () => import('./aisle/aisle.module').then(m => m.AisleModule)
      },
      {
        path: 'area',
        loadChildren: () => import('./area/area.module').then(m => m.AreaModule)
      },
      {
        path: 'level',
        loadChildren: () => import('./level/level.module').then(m => m.LevelModule)
      },
      {
        path: 'bay',
        loadChildren: () => import('./bay/bay.module').then(m => m.BayModule)
      },
      {
        path: 'carrier',
        loadChildren: () => import('./carrier/carrier.module').then(m => m.CarrierModule)
      },
      {
        path: 'order-type',
        loadChildren: () => import('./order-type/order-type.module').then(m => m.OrderTypeModule)
      },
      {
        path: 'shipping-address',
        loadChildren: () => import('./shipping-address/shipping-address.module').then(m => m.ShippingAddressModule)
      },
      {
        path: 'unit',
        loadChildren: () => import('./unit/unit.module').then(m => m.UnitModule)
      },
      {
        path: 'tag',
        loadChildren: () => import('./tag/tag.module').then(m => m.TagModule)
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MasterRoutingModule { }
