declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { errorMessage } from 'src/app/messages';
import { PaginationService } from 'src/app/service/pagination.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CommonFunction } from 'src/app/common';
import { BrandService } from 'src/app/service/brand.service';

@Component({
  selector: 'app-brand-list',
  templateUrl: './brand-list.component.html'
})
export class BrandListComponent implements OnInit {

  // listing
  loadingState = true;
  objectArray: Array<any> = [];
  questionArray: Array<any> = [];
  pagination: any = null;
  showPagination: boolean = false;
  searchText: any = null;
  currentPage: any = 1;


  // form
  addForm: FormGroup;
  formErrors = {
    brand_name: null,
    apierror: null
  };
  showLoader = false;
  showAddForm = false; // add
  isEditing = false; // edit
  editId: string = null; // edit
  editIndex: string = null; // edit
  loadform = false;
  permissionObject: any = null;

  constructor(
    private paginationService: PaginationService,
    private BrandService: BrandService,
    private fBuilder: FormBuilder,
  ) {
    this.addForm = this.fBuilder.group({
      'brand_name': [null, Validators.compose([Validators.required])],
    });
  }
  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['company'];
    // });
    this.loadform = false;
    this.getObjects();
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.BrandService.getBrandList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);

      } else {
        response.error.filter(() => {

        });
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }



  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.BrandService.deleteBrand(object.brand_id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

  editObject(object, index) {
    this.resetForm();
    this.editIndex = index;
    this.isEditing = true;
    this.editId = object.brand_id;
    this.addForm.patchValue({
      'brand_name': object.brand_name,
    });
  }



  showForm() {
    this.resetForm();
    this.showAddForm = true;
  }

  resetForm() {
    this.showAddForm = false;
    this.isEditing = false;
    this.editId = null;
    CommonFunction.resetForm(this.addForm, this.formErrors);
  }


  submitForm(): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      const formData = {
        'brand_name': this.addForm.value.brand_name,
      };
      if (this.isEditing) {

        this.BrandService.editBrand(this.editId, formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.getObjects();
            this.resetForm();
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('brand_name')) {
                this.formErrors['brand_name'] = obj['brand_name'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.formErrors['apierror'] = `* ${error.error}`;
          this.showLoader = false;
        });
      } else {
        this.BrandService.addBrand(formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.resetForm();
            this.getObjects();
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('brand_name')) {
                this.formErrors['brand_name'] = obj['brand_name'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.formErrors['apierror'] = `* ${error.error}`;
          this.showLoader = false;
        });
      }
    }
  }
}





