import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { BinService } from 'src/app/service/bin.service';

@Component({
  selector: 'app-bin-list',
  templateUrl: './bin-list.component.html',
})
export class BinListComponent implements OnInit {

  PageTitle: string = 'Manage Bin';

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  //For Dynamic List
  headerData: Array<any> = [];
  showPagination: boolean = false;
  constructor(
    private paginationService: PaginationService,
    private binService: BinService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Bin Name",
        key: "bin_name",
      }),
      new TextField({
        label: "Bin Code",
        key: "bin_code",
      }),
      new TextField({
        label: "Bin Tag",
        key: "tag_id",
      }),
      new TextField({
        label: "Area",
        key: "area.label",
      }),
      new TextField({
        label: "Aisle",
        key: "aisle.label",
      }),
      new TextField({
        label: "Level",
        key: "level.label",
      }),
      new TextField({
        label: "Bin Size",
        key: "bin_size",
      }),
      new TextField({
        label: "Bin Volume",
        key: "bin_volume",
      }),
      new TextField({
        label: "Avliable Volume",
        key: "available_volume",
      })
    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/bin/edit/',
        id: 'bin_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'bin/',
        id: 'bin_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.binService.getBinList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}






